  /* get the styles for both icon*/
@import "https://cssicon.space/css/icons.css";
body{
  color: #ffffff;
}

@media (max-width: 576px) {

  .nav{
    padding: 0;
    padding-left: 10px;
  }

  .logo-text-container{
    display: flex;
    flex-direction: column;
    column-gap: 3px;
  }

  .logo-text-container > p{
    margin: 0;
  }

  .developer-p{
    padding-left: 0px;
    border-left: none;
  }

  .nav-left-section{
    padding: 0 ;
  }

  .nav-right-section{
    display: flex;
    flex-direction: row;
    align-items: center;

    padding: 0;
    height: 100%;
  }

  .hamburger-menu-container{
    height: 20px;
    width: 30px;
    display: block;
    margin-right: 10px;
  }

  .icon, .menu{
    height: 100%;
    width: 100%;

    display: block;
  }

  .menu.icon{
    color: #ffffff; 
  }

  .icon, .icon:before, .icon:after, .icon i, .icon i:before, .icon i:after { 
    transition: all 0.4s ease;
    color: #ffffff;
  }
  /* this is merely a rough demo to showcase the potential of CSS ICON on animation, more crafted animation can go from here */

  .about, .work, .contact{
    display: none;
  }

  .hamburger-dropdown{
    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: #8000ff25;

    position: absolute;
    right: 0;
    top: 60px;

    width: 100%;
    overflow: hidden;          /* important for height animation */
    max-height: 0;             /* hidden by default */
    opacity: 0;                /* hidden */
    padding: 0; 

    transition: all 0.4s ease;
  }

  .about-dropdown,
  .work-dropdown,
  .contact-dropdown{
    padding: 5px 0;
    width: 100%;
  }

  .about-dropdown, .work-dropdown, .contact-dropdown{
    width: 100%;
  }

  .about-dropdown:hover, .work-dropdown:hover{
    border-bottom: solid 1px #ffffff;
  }
  .work-dropdown:hover, .contact-dropdown:hover{
    border-top: solid 1px #ffffff;
  }

  .show {
    max-height: 500px;        /* enough to fit your menu items */
    opacity: 1;
    padding: 10px 0;          /* restore padding */
  }
}

