.nav{
  height: 60px;
  padding: 0 50px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  background-color: #000000;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

.logo-text-container{
  display: flex;
  flex-direction: row;
  column-gap: 10px;
  margin-left: 10px;
}

.logo-text-container > p{
  height: 60%;

  display: flex;
  align-items: center;
}

.developer-p{
  padding-left: 10px;
  border-left: solid 3px #ffffff;
}

.nav-left-section{
  display: flex;
  flex-direction: row;
  align-items: center;

  padding: 0 20px;
  height: 100%;
}

.nav-right-section{
  display: flex;
  flex-direction: row;
  align-items: center;

  padding: 0 20px;
  height: 100%;
}

.hamburger-menu-container{
  height: 20px;
  width: 20px;
}

.menu{
  display: none;
  color: #ffffff;
}

.about, .work, .contact{
  padding: 0 10px;
  height: 60%;

  display: flex;
  align-items: center;
  cursor: pointer;
}

.about:hover, .work:hover, .contact:hover{
  border-right: solid 1px #afafaf;
  border-left: solid 1px #afafaf ;
}

.nav-right-section a,
.nav-right-section a:visited,
.nav-right-section a:hover,
.nav-right-section a:active {
  text-decoration: none;  /* Remove underline */
  color: inherit;         /* Match nav's text color */
}

.header-logo{
  height: 30px;
  width: 30px;
}