@media (max-width: 576px) {
  .main-content{
    height: fit-content;
    margin: 10% 0;
    padding: 0;
    width: 100%;
  }

  .presentation{
    display: flex;
    flex-direction: row;
    align-items: center;
    
    width: 100%;
    height: 110vh;
    margin-top: 0px;
  }

  .vertical-line-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px; /* spacing */
    margin-right: 3px;
  }

  /* Circle at the top */
  .vertical-line-container .circle {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #640494; /* solid violet circle */
    margin-bottom: 0;
    z-index: 2;
  }

  /* Vertical line */
  .vertical-line-container .line {
    width: 2px; /* line thickness */
    height: 40vh; /* line length */
    background: linear-gradient(to bottom, #640494, transparent);
    z-index: 1;
  }

  .presentation-text{
    margin-top: -27vh;
  }
  .presentation-header{
    font-size: 40px;
    margin-bottom: 0;
  }
  .name{
    color: #640494;
  }
  .presentation-p{
    margin-top: 5px;
    width: 70%;
    font-size: 17px;
  }
}

