@media (max-width: 576px) {
  .form-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: fit-content;
    width: 100%;
    margin: 10% auto;
    margin-bottom: 0;
  }

  .form{
    height: fit-content;
    width: 80%;
    margin: 0 auto;
    margin-bottom: 10%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 20px;
  }

  .form-header{
    font-size: 50px;
    margin-bottom: 20px;
  }

  .name-input-container,
  .email-input-container,
  .message-input-container{
    margin: 10px 0;
    width: 90%;
  }

  .name-label,
  .email-label,
  .message-label{
    top: 30%;
    left: 20px;
  }

  .name-label-focused,
  .email-label-focused,
  .message-label-focused{
    top: -7px;
    left: 8px;
    font-size: 12px;
  }

  .status-message-container{
    width: 90%;
    height: 70px;
    text-align: center;
    font-size: 14px;
  }

  .submit-button{
    font-weight: 500;
  }

  .globe-container{
    height: 60%;
    justify-content: start;
  }

  .globe-image{
    height: 384px;
    width: 256px;
    transition: all 0.6s ease;
  }

  .globe-image:hover{
    transform: rotateX(-3deg) scale(1.02);
  }
}

